.contacts {
  padding-top: 50px;
  background-color: $white;
  color: $dark-blue-desaturarion;
}

.contacts__lead {
  margin-bottom: 45px;
}

.contacts__wrapper {
  margin-bottom: 41px;
}

.contacts__title {
  margin: 0;
  margin-bottom: 13px;
  color: $black;
  font-size: 18px;
  font-weight: 700;
  text-align: center;
}

.contacts__link {
  display: block;
  margin-bottom: 31px;
  color: $pink;
  font-size: 24px;
  font-weight: 300;
  text-align: center;
  text-decoration: none;
}

.contacts__map {
  display: block;
}
